.panel-content {
  position: relative;
  flex-grow: 1;
  height: 100%;
  padding: 36px 41px 28px;
  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 #dee5f0;

  :global {
    .ant-descriptions-item {
      padding-top: 8px;
    }

    .ant-switch-checked {
      background-color: #6abe42;
    }
  }

  &-btns {
    position: absolute;
    right: 41px;
    bottom: 28px;
    left: 41px;
    text-align: right;

    > button {
      margin-left: 16px;
    }
  }
}
