.panel-content {
  position: relative;
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 36px 41px 28px;
  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 #dee5f0;

  :global {
    .ant-empty-description {
      color: #999;
      font-size: 16px;
    }
  }
}
