.panel-content {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 380px;
  height: 100%;
  margin-right: 12px;
  padding: 24px 20px 22px;
  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 #dee5f0;

  &-search {
    display: flex;
    justify-content: space-between;

    &-input {
      margin-right: 12px;
      background: #fafafa;
      border: none;
      border-radius: 4px;

      :global {
        .ant-input {
          background: #fafafa;
        }
      }
    }

    &-btn {
      flex-shrink: 0;
      border: none;
      border-radius: 4px;
      box-shadow: 0 1px 4px 0 rgba(0, 21, 41, 0.12);
    }
  }

  &-tree {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    margin-top: 20px;

    &-loading {
      margin-bottom: 12px;
    }

    &-items {
      flex-grow: 1;

      height: 0;
      padding: 22px 12px;
      overflow: auto;
      border: 1px solid rgba(0, 0, 0, 0.08);
      border-radius: 4px;

      .wrapper {
        position: relative;
        display: flex;
        padding: 10px;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.69px;

        > .leaf {
          position: absolute;
          top: 12px;
          right: 4px;
          z-index: 99;
          width: 38px;
          height: 18px;
          display: flex;
          align-items: center;
          justify-content: center;
          color: rgba(0, 0, 0, 0.25);
          color: #46a0c0;
          font-weight: 400;
          font-size: 12px;
          font-family: PingFangSC-Regular;
          letter-spacing: 0.3px;
          text-align: center;
          border: 1px solid #46a0c0;
          border-radius: 10px;
        }
      }

      :global {
        // .ant-tree-treenode-switcher-close {
        //     position: relative;
        // }
        .ant-tree-treenode {
          position: relative;
        }

        .ant-badge-status-dot {
          width: 8px;
          height: 8px;
        }

        .ant-tree-switcher {
          position: absolute;
          top: 10px;
          right: 0;
          z-index: 99;
          color: rgba(0, 0, 0, 0.25);
        }

        .ant-tree-node-selected {
          color: #217ba0;
          background-color: #effaff !important;
        }

        .ant-tree-treenode-selected {
          position: relative;

          &::after {
            position: absolute;
            top: 8px;
            right: -12px;
            width: 5px;
            height: 28px;
            background-color: #46a0c0;
            border-radius: 1.5px;
            content: '';
          }
        }

        .ant-tree-node-content-wrapper:hover {
          background-color: #effaff;
        }
      }
    }
  }
}
