@import "@/style/var";

.panel-content {
  position: relative;
  flex-grow: 1;
  height: 100%;
  padding: 36px 21px 28px;
  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 #dee5f0;

  :global {
    .ant-descriptions-view {
      padding: 10px;
    }

    .ant-descriptions-item {
      padding-top: 8px;
    }

    .ant-switch-checked {
      background-color: #6abe42;
    }
  }

  &-detail {
    :global {
      .ant-descriptions-view {
        background-color: @back-color;
      }
    }
  }

  &-btns {
    position: absolute;
    right: 41px;
    bottom: 28px;
    left: 41px;
    text-align: right;

    @media screen and (max-height:800px) {
      position: static;
      margin-top: 12px;
    }

    >button {
      margin-left: 16px;
    }
  }

  &-map {
    display: flex;
    max-width: 920px;

    &-des {
      margin-right: 24px;
    }

    &-com {
      flex-shrink: 0;
      width: 350px;
      height: 288px !important;
    }
  }
}