@import "@/style/var";

.panel-content {
  position: relative;
  flex-grow: 1;
  height: 100%;
  padding: 36px 41px 28px;
  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 #dee5f0;
  overflow: auto;

  h3 {
    flex: auto;
    overflow: hidden;
    color: @break-color2;
    font-weight: bold;
    font-size: 16px !important;
    line-height: 1.5715;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  &-form {
    margin-top: 20px;

    &-switch {
      position: absolute;
      top: 36px;
      right: 41px;
    }
  }

  :global {
    .ant-descriptions-item {
      padding-top: 8px;
    }

    .ant-form-item-label>label {
      color: @break-color3;
    }

    .ant-switch-checked {
      background-color: @green-color;
    }

    textarea {
      resize: none;
    }
  }

  &-btns {
    position: absolute;
    right: 41px;
    bottom: 28px;
    left: 41px;
    text-align: right;

    @media screen and (max-height:800px) {
      position: static;
      margin-top: 12px;
    }

    >button {
      margin-left: 16px;
    }
  }


  &-map {
    display: flex;
    width: 100%;
    height: 288px;
  }
}